@import '../../styles/variables.scss';

.loader {
  border: 8px solid #f3f3f3;
  border-top: 8px solid $primary-color;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 2s linear infinite;
  justify-self: center;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}